.about-program {
    background-image: url(../../img/watercolor_bg.jpg);
    background-repeat: no-repeat;
    background-position: bottom;
    width: 100%;
    min-height: 800px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 55px;
    box-sizing: border-box;

    h2 {
        margin: 0;
        @include title;
    }

    p {
        margin: 0;
        @include firaSans17;
        width: 690px;
        text-align: center;
    }

    .about-program__video-block {
        position: relative;

        button {
            display: block;
            position: absolute;
            width: 96px;
            height: 96px;
            border: none;
            border-radius: 50%;
            background-color: rgba(255, 255, 255, 0.2);
            top: 37%;
            left: 44%;

            &:hover {
                cursor: pointer;
            }

            &::after {
                content: '';
                display: block;
                position: absolute;
                border: 10px solid transparent;
                border-left: 15px solid #1FC0FC;
                top: 37px;
                left: 43px;
            }

            &::before {
                content: "";
                position: absolute;
                width: 64px;
                height: 64px;
                border: none;
                border-radius: 50%;
                background-color: #fff;
                top: 16px;
                left: 16px;
            }
        }
    }
}

@media (max-width: 1024px) {
    .about-program {
        min-height: 600px;

        @media (max-width: 900px) {
            padding-bottom: 20px;
            min-height: 500px;
        }

        h2 {
            font-size: 32px;
            margin-bottom: 10px;

            @media (max-width: 900px) {
                font-size: 32px;
            }
        }

        p {
            font-size: 16px;

            @media (max-width: 900px) {
                font-size: 14px;
            }
        }

        padding-left: 50px;

        .about-program__video-block {
            .about-program__video-block-video {
                max-width: 95%;
                height: auto;
            }
        }
    }
}

@media (max-width: 767px) {
    .about-program {
        min-height: 500px;
        padding: 0;
        background-position: center 0;

        p {
            width: 86%;
            text-align: left;
        }

        .about-program__video-block {
            button {
                width: 80px;
                height: 80px;

                &::after {
                    border: 7px solid transparent;
                    border-left: 12px solid #1FC0FC;
                    top: 34px;
                    left: 37px;
                }

                &::before {
                    width: 53.3px;
                    height: 53.3px;
                    top: 14px;
                    left: 14px;
                }
            }

            .about-program__video-block-video {
                max-width: 100%;
            }
        }
    }
}

.programs {
    max-width: 100%;
    padding: 80px 22.3% 20px 15.4%;
    margin: 0 auto;
    background-image: url(../../img/sketch_bg.jpg);
    background-repeat: no-repeat;
    background-size: 680px 426px;
    background-position: right 50px;

    h2 {
        margin: 0 0 60px 0;
        @include title;
    }

    .programs__content {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;

        .programs__content-buttons-block {
            width: 230px;
            height: 350px;
            padding-right: 50px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            .programs__content-buttons-block-btn {
                height: 68px;
                padding: 16px 20px 17px 20px;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                border: none;
                background-color: transparent;
                @include firaSans15;
                opacity: .87;

                &:hover {
                    width: 200px;
                    height: 68px;
                    position: relative;
                    background-image: linear-gradient(138.15deg, #FFC341 20.46%, #FFD701 65.31%);
                    cursor: pointer;
                }

                &:hover:after {
                    content: "";
                    display: block;
                    position: absolute;
                    border: 34px solid transparent;
                    border-left: 28px solid #FFD701;
                    top: 0;
                    right: -62px;
                }

                &:focus {
                    outline: none;
                }

                svg {
                    margin-right: 20px;
                }
            }
        }

        .programs__content-buttons-block-btn-active {
            width: 200px;
            height: 68px;
            position: relative;
            border: none;
            background-image: linear-gradient(138.15deg, #FFC341 20.46%, #FFD701 65.31%);
            //transform: matrix(1, 0, 0, -1, 0, 0);
            font-size: 16px;
            line-height: 19px;
            font-variant: small-caps;
            color: $color-white;

            &::after {
                content: "";
                display: block;
                position: absolute;
                border: 34px solid transparent;
                border-left: 28px solid #FFD701;
                top: 0px;
                right: -62px;
            }
        }

        .programs__content-txt-blocks-block {
            width: 560px;

            &:nth-of-type(2),
            &:nth-of-type(3),
            &:nth-of-type(4),
            &:nth-of-type(5) {
                display: none;
            }

            h3 {
                margin-top: 10px;
                ;
                @include title;
                font-size: 28px;
                line-height: 34px;
                text-transform: none;
            }

            p {
                @include firaSans15;
                line-height: 170%;
                opacity: .87;
            }
        }
    }
}

@media (max-width: 1024px) {
    .programs {
        padding: 80px 9% 20px 8%;

        h2 {
            font-size: 32px;
        }
    }
}

@media (max-width: 767px) {
    .programs {
        padding: 100px 17px 0px;
        background-position: right 300px;

        .programs__content {
            flex-wrap: wrap;

            .programs__content-buttons-block {
                width: 100%;
