.howtogo {
    width: 100%;
    min-height: 1200px;
    padding: 14% 0 50px 52%;
    box-sizing: border-box;
    background-image: url(../../img/img-howtogo.png), url(../../img/stamp.png), url(../../img/watercolor_bg2.jpg);
    background-size: 42.5% auto, 18.6% auto, 100%;
    background-repeat: no-repeat, no-repeat, no-repeat;
    background-position: 0px 170px, right 400px, left -30px;

    .howtogo__content {
        max-width: 540px;

        h2 {
            @include title;
            margin-top: 0;
        }

        p {
            max-width: 480px;
            @include firaSans15;
            line-height: 170%;
            opacity: .87;
        }

        h3 {
            @include firaSans17;
        }

        ol {}

        ol li {
            height: 50px;
            margin: 30px 0 0 10px;
            position: relative;
            @include firaSans15;
            line-height: 140%;
            opacity: .87;
            list-style-type: none;
        }

        ol li::before {
            position: absolute;
            top: -5px;
            left: -60px;
            @include markerforli;
        }

        ol li:nth-of-type(1):before {
            content: "1";
        }

        ol li:nth-of-type(2):before {
            content: "2";
        }

        ol li:nth-of-type(3):before {
            content: "3";
        }

        ol li:nth-of-type(4):before {
            content: "4";
        }

        ol li::after {
            content: "";
            display: block;
            height: 43px;
            border-left: 1px dashed #484848;
            position: absolute;
            opacity: .2;
            left: -42px;
            top: 32px;
        }

        ol li:nth-of-type(4):after {
            display: none;
        }

        span {
            display: block;
            padding-left: 20px;
            margin: 50px 0 0 5px;
            @include firaSans15;
            line-height: 140%;
            color: rgba(72, 72, 72, .5);
            border-left: 4px solid #FFC341;
        }
    }

}

