.FAQ {
    min-height: 880px;
    padding: 50px 0 12% 8%;
    box-sizing: border-box;
    background-image: url(../../img/bag.png), url(../../img/faq.png), url(../../img/watercolor_bg4.jpg);
    background-size: 167px 177px, 47.2% auto, 100%;
    background-repeat: no-repeat;
    background-position: left 675px, right 50px, top;

    h2 {
        margin-top: 0;
        @include title;
    }

    details {
        width: 560px;
        position: relative;
        padding: 30px 0 30px 30px;
        box-sizing: border-box;
        border-bottom: 1px solid rgba(72, 72, 72, .2);

        summary {
            width: 520px;
            padding-right: 150px;
            box-sizing: border-box;
            list-style: none;
            cursor: pointer;
            @include firaSans17;

            &::after {
                content: "";
                display: block;
                width: 10px;
                height: 10px;
                position: absolute;
                top: 33px;
                right: 30px;
                border: 2px solid #1FC0FC;
                border-color: #1FC0FC #1FC0FC transparent transparent;
                transform: rotate(135deg);
            }
        }

        summary::-webkit-details-marker {
            display: none;
        }

        summary:focus {
            outline: none;
        }

        p {
            width: 480px;
            margin-left: -30px;
            @include firaSans15;
            line-height: 140%;
            opacity: .74;
        }

        &::before {
            @include markerfordetails;
        }

        &:nth-of-type(1):before {
            content: "1";
        }

        &:nth-of-type(2):before {
            content: "2";
        }

        &:nth-of-type(3):before {
            content: "3";
        }

        &:nth-of-type(4):before {
            content: "4";
        }

        &:nth-of-type(5):before {
            content: "5";
        }

        &:nth-of-type(6):before {
            content: "6";
        }
    }

    details[open] summary::after {
        transform: rotate(-45deg);
        top: 42px;
    }
}

@media (max-width: 1300px) {
    .FAQ {
        details {
            width: 500px;
            padding: 20px 0 20px 30px;

            &::before {
                top: 9px;

            }

