                        height: 22px;
                        background-image: url(../../img/sm.svg);

                        &:nth-of-type(1) {
                            background-position: 0px;
                        }

                        &:nth-of-type(2) {
                            background-position: 65px;
                        }

                        &:nth-of-type(3) {
                            background-position: 22px;
                        }
                    }
                }
            }
        }
    }

    &::after {
        content: "";
        overflow: hidden;
    }
}

@media (max-width: 1200px) {
    .reviews {
        padding: 0 14% 100px 7.5%;

        h2 {
            margin-bottom: 50px;
        }

        .reviews__slider {
            img {
                margin-top: 20px;
            }

            .reviews__slider-figcaption {
                padding-top: 0;
            }

            p {
                @media (max-width: 1100px) {
                    margin-top: 0;
                }
            }
        }
    }
}

@media (max-width: 1024px) {
    .reviews {
        padding: 0 3% 100px 2%;

        .reviews__slider {
            .reviews__slider-figcaption {
                p {
                    font-size: 16px;
                }
            }
        }
    }
}

@media (max-width: 767px) {
    .reviews {
        background-position: left 700px, right 800px;

        .reviews__slider {
            padding: 0 8%;

            img {
                width: 100%;
                height: auto;
                margin: 0 auto;
            }

            .reviews__slider-pagination {
                display: flex;
            }

            .reviews__slider-figcaption {
                clear: both;

                .reviews__slider-figcaption-row {
                    .reviews__slider-figcaption-row-pagination {
                        display: none;
                    }
                }
            }
        }
    }
}

.details {
    padding: 0 8.3% 80px 8%;
    box-sizing: border-box;
    background-image: url(../../img/map.png), url(../../img/watercolor_bg6.jpg);
    background-size: 51.4% auto, 100%;
    background-position: right 90px, top;
    background-repeat: no-repeat;

    h2 {
        @include title;
        margin: 38px 0 45px 0;
    }

    span {
        display: inline-block;
        vertical-align: middle;
        @include firaSans15;
        opacity: .74;
        margin: 0 0 0 5px;

        a {
            width: 95px;
            padding-bottom: 1px;
            border-bottom: 1.5px dashed #1FC0FC;
            font-weight: 500;
            font-variant: small-caps;
            margin: 0 10px 0 10px;
            color: $color-txt;
            text-decoration: none;
        }
    }

    .details__curator {
        width: 400px;
        margin: 45px 0 0 0;
        display: flex;
        justify-content: space-between;

        .details__curator-figcaptoin {
            width: 250px;

            .details__curator-figcaptoin-name {
                margin: 5px 0 13px;
                @include firaSans15;

                span {
                    color: #1FC0FC;
                    margin: 0 10px 0 0;
                }
            }

            .details__curator-figcaptoin-address {
                @include firaSans15;
                font-size: 13px;
                opacity: .74;
            }

            .details__curator-figcaptoin-socials {
                display: flex;
                justify-content: space-between;
                width: 100px;

                a {
                    display: block;
                    width: 22px;
                    height: 22px;
                    margin-top: 18px;
                    background-image: url(../../img/sm2.svg);

                    &:nth-of-type(1) {
                        background-position: 0px;
                    }

                    &:nth-of-type(2) {
                        background-position: 59px;
                    }

                    &:nth-of-type(3) {
                        background-position: 22px;
                    }
                }
            }
        }
    }

    p {
        width: 480px;
        @include firaSans17;
        opacity: .87;
        margin: 45px 0 130px 0;
    }

    .details__form {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        min-height: 100px;

        p {
            width: 355px;
            font-weight: 500;
            margin: 0;

            span {
                opacity: .5;
                margin: 15px 0 0 0;
            }
        }

        form input[type="text"] {
            width: 250px;
            height: 59px;
            margin-right: 20px;
            padding: 0 0 0 40px;
            box-sizing: border-box;
            border-radius: 80px;
            border: 2px solid #E3E3E3;
            @include firaSans17;
            font-weight: 500;
            opacity: .5;

            &:focus {
                outline: none;
            }
        }

        form input[type="text"]:nth-of-type(2) {
            border-color: #FFD701;
            opacity: .87;
        }

        form input[type="submit"] {
            @include btn;
            width: 216px;

            &:focus {
                outline: none;
            }

            &:hover {
                cursor: pointer;
                opacity: .8;
            }
        }
    }
}

@media (max-width: 1360px) {
    .details {
        background-position: right 150px, top;

        p {
            width: 430px;
            @include firaSans15;
            line-height: 30px;
            opacity: .87;
            margin: 45px 0 130px 0;
        }

        .details__form {
            p {
                width: 300px;

                span {
                    font-size: 13px;
                }
            }

            form input[type="text"] {
                width: 220px;
                height: 54px;
                @include firaSans15;
                margin-right: 10px;

                @media (max-width: 1150px) {
                    width: 180px;
                    height: 50px;
                    font-size: 13px;
                }
            }

            form input[type="submit"] {
                @include btn;
                width: 180px;
                height: 54px;
                font-size: 15px;

                @media (max-width: 1150px) {
                    width: 150px;
                    height: 50px;
                    font-size: 13px;
                }
            }
        }
    }
}

@media (max-width: 1023px) {
    .details {
        background-position: right 80px, top;

        @media (max-width: 860px) {
            .details__curator {
                margin: 25px 0 0 0;
                width: 370px;
            }
        }

        p {
            width: 430px;
            margin-bottom: 50px;
        }

        .details__form {
            flex-wrap: wrap;
            justify-content: center;

            p {
                width: 550px;
                margin-bottom: 20px;
            }

        }
    }
}

@media (max-width: 767px) {
    .details {
        background-image: url(../../img/map.png);
        background-size: 100%;
        background-position: center 650px;

        h2 {
            font-size: 32px;
            text-align: center;
        }

        .details__curator {
            flex-direction: column;
        }

        p {
            width: 90%;
            margin-bottom: 80vw;
        }

        .details__form {
            form {
                height: 200px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                align-items: center;

                input[type="text"] {
                    width: 288px;
                    height: 59px;
                    @include firaSans17;
                    margin: 0;
                }

                input[type="submit"] {
                    width: 264px;
                    height: 59px;
                    font-size: 17px;
                }
            }
        }
    }
}
